"use client";

import { useEffect, useState } from "react";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Plus, Search, Edit3, Trash2, Package, Camera } from "lucide-react";
import { getProducts, upsertProduct, deleteProduct } from "@/lib/actions";
import { CameraScanner } from "@/components/shared/CameraScanner";

export default function ProductsManagement() {
    const [products, setProducts] = useState<any[]>([]);
    const [isOpen, setIsOpen] = useState(false);
    const [selectedProduct, setSelectedProduct] = useState<any>(null);
    const [searchTerm, setSearchTerm] = useState("");
    const [showScanner, setShowScanner] = useState(false);
    const [scannedBarcode, setScannedBarcode] = useState("");

    useEffect(() => {
        fetchProducts();
    }, []);

    async function fetchProducts() {
        const data = await getProducts();
        setProducts(data);
    }

    async function handleSubmit(e: React.FormEvent<HTMLFormElement>) {
        e.preventDefault();
        const formData = new FormData(e.currentTarget);
        const data = {
            id: selectedProduct?.id,
            name: formData.get("name"),
            barcode: formData.get("barcode"),
            price: formData.get("price"),
            stock: formData.get("stock"),
            category: formData.get("category"),
        };
        await upsertProduct(data);
        fetchProducts();
        setIsOpen(false);
    }

    const filteredProducts = products.filter(p =>
        p.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        p.barcode.includes(searchTerm)
    );

    return (
        <div className="space-y-6">
            <header className="flex justify-between items-end">
                <div>
                    <h1 className="text-3xl font-black tracking-tight uppercase flex items-center gap-2">
                        <Package className="w-8 h-8 text-primary" /> Inventory
                    </h1>
                    <p className="text-muted-foreground">Manage your products, prices, and stock levels</p>
                </div>
                <Button
                    onClick={() => {
                        setSelectedProduct(null);
                        setIsOpen(true);
                    }}
                    className="font-bold flex gap-2"
                >
                    <Plus className="w-4 h-4" /> ADD PRODUCT
                </Button>
            </header>

            <Card className="border-border shadow-md">
                <CardContent className="p-6">
                    <div className="flex gap-4 mb-6">
                        <div className="relative max-w-sm w-full">
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                            <Input
                                placeholder="Search by name or barcode..."
                                className="pl-10"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                        </div>
                    </div>

                    <Table>
                        <TableHeader>
                            <TableRow className="bg-muted/50 transition-none hover:bg-muted/50">
                                <TableHead>PRODUCT NAME</TableHead>
                                <TableHead>BARCODE</TableHead>
                                <TableHead>PRICE</TableHead>
                                <TableHead>STOCK</TableHead>
                                <TableHead className="text-right">ACTIONS</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {filteredProducts.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={5} className="text-center h-24 text-muted-foreground italic">
                                        No products found
                                    </TableCell>
                                </TableRow>
                            ) : null}
                            {filteredProducts.map((product) => (
                                <TableRow key={product.id} className="hover:bg-muted/30 transition-colors">
                                    <TableCell className="font-bold tracking-tight">{product.name}</TableCell>
                                    <TableCell className="font-mono text-xs">{product.barcode}</TableCell>
                                    <TableCell className="font-bold text-primary">â‚±{product.price.toFixed(2)}</TableCell>
                                    <TableCell>
                                        <span className={`font-mono font-bold ${product.stock < 10 ? "text-destructive" : ""}`}>
                                            {product.stock}
                                        </span>
                                    </TableCell>
                                    <TableCell className="text-right">
                                        <div className="flex justify-end gap-2">
                                            <Button variant="ghost" size="icon" onClick={() => {
                                                setSelectedProduct(product);
                                                setIsOpen(true);
                                            }}>
                                                <Edit3 className="w-4 h-4" />
                                            </Button>
                                            <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive hover:bg-destructive/10" onClick={async () => {
                                                if (confirm("Delete this product?")) {
                                                    await deleteProduct(product.id);
                                                    fetchProducts();
                                                }
                                            }}>
                                                <Trash2 className="w-4 h-4" />
                                            </Button>
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>

            <Dialog open={isOpen} onOpenChange={setIsOpen}>
                <DialogContent className="sm:max-w-[425px] bg-background">
                    <form onSubmit={handleSubmit}>
                        <DialogHeader>
                            <DialogTitle className="text-2xl font-black uppercase tracking-tight">
                                {selectedProduct ? "Edit Product" : "Add New Product"}
                            </DialogTitle>
                        </DialogHeader>
                        <div className="grid gap-4 py-4">
                            <div className="grid gap-2">
                                <label className="text-sm font-bold uppercase tracking-widest text-muted-foreground">Product Name</label>
                                <Input name="name" defaultValue={selectedProduct?.name} required placeholder="e.g. Classic Burger" />
                            </div>
                            <div className="grid gap-2">
                                <label className="text-sm font-bold uppercase tracking-widest text-muted-foreground">Barcode</label>
                                <div className="flex gap-2">
                                    <Input
                                        name="barcode"
                                        value={scannedBarcode || selectedProduct?.barcode || ""}
                                        onChange={(e) => setScannedBarcode(e.target.value)}
                                        required
                                        placeholder="Scan or type barcode"
                                        className="font-mono"
                                    />
                                    <Button
                                        type="button"
                                        variant="secondary"
                                        size="icon"
                                        onClick={() => setShowScanner(true)}
                                        className="shrink-0"
                                    >
                                        <Camera className="w-4 h-4" />
                                    </Button>
                                </div>
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <div className="grid gap-2">
                                    <label className="text-sm font-bold uppercase tracking-widest text-muted-foreground">Price (â‚±)</label>
                                    <Input name="price" type="number" step="0.01" defaultValue={selectedProduct?.price} required placeholder="0.00" />
                                </div>
                                <div className="grid gap-2">
                                    <label className="text-sm font-bold uppercase tracking-widest text-muted-foreground">Stock</label>
                                    <Input name="stock" type="number" defaultValue={selectedProduct?.stock} required placeholder="0" />
                                </div>
                            </div>
                            <div className="grid gap-2">
                                <label className="text-sm font-bold uppercase tracking-widest text-muted-foreground">Category</label>
                                <Input name="category" defaultValue={selectedProduct?.category} placeholder="e.g. Food" />
                            </div>
                        </div>
                        <DialogFooter>
                            <Button type="button" variant="outline" onClick={() => {
                                setIsOpen(false);
                                setScannedBarcode("");
                            }}>CANCEL</Button>
                            <Button type="submit" className="font-bold">SAVE PRODUCT</Button>
                        </DialogFooter>
                    </form>
                </DialogContent>
            </Dialog>

            {showScanner && (
                <CameraScanner
                    onScan={(barcode) => {
                        setScannedBarcode(barcode);
                        setShowScanner(false);
                    }}
                    onClose={() => setShowScanner(false)}
                />
            )}
        </div>
    );
}
